a{
text-decoration: none;
color: var(--second-bgr-color);    
}

h1{
    font-family: 'Titan One';
    color: var(--second-bgr-color);
    letter-spacing: 0.04em;
    font-style: normal;
    font-weight: 400;
    text-transform: uppercase;   
}

.header{
    display: flex;
    align-items: center;    

    &__link{
    transition: color 250ms var(--timing-function);
    }
    &--background{        
        background-color: var(--header-bgr-color);      
    }
}

 .header__menu {
     display: flex;
     flex-direction: column-reverse;
     justify-content: flex-end;
     position: fixed;        
     transform: translateX(100vw);    

     top: 0;
     left: 40%;
     right: 0;
     width: 254px;
     visibility: hidden;
     opacity: 0;
     background-color: rgba(225, 121, 146, 1);
     height: 100%;
    //  transition: transform 250ms var(--timing-function);
     z-index: 5;

     &--icon-close{
        transition: 250ms var(--timing-function);

        &:hover{
            box-shadow: 5px 5px 5px var(--primary-accent-color);
        }
     }
 }

 .header__menu.is-open {
     transform: translateX(0);
     visibility: visible;
     opacity: 1;
     transition: transform 400ms var(--timing-function);
 }


 

//  .header__input:checked~.header__menu {
//      transform: translate(0, 0);
//  }

.nav{
    display: flex;

    &__links{        
        list-style:none;

        &--desktop{
            display: none;
        }               
    }    

    &__button{
        display: flex;
        height: 44px;
        width: 160px;
        align-items: center;
        justify-content: center;
        background-color: var(--second-bgr-color);
        color: var(--primary-accent-color);
        border: none;
        border-radius: 22px;
        font-weight: 700;
        cursor: pointer;
        fill: var(--primary-accent-color);

        &:hover,
        &:focus{
            background-color:var(--primary-accent-color);
            color: var(--second-bgr-color);
            fill: var(--second-bgr-color);           
        }

        &-arrow{
            width: 9px;
            height: 11px;
            margin-left: 12px;
        }
    }
}
.header__button{
    display: flex;
    height: 44px;
    width: 160px;
    align-items: center;
    justify-content: center;
    background-color: var(--second-bgr-color);
    color: var(--primary-accent-color);
    fill:var(--primary-accent-color) ;
    border: none;
    cursor: pointer;
    border-radius: 22px;
    font-weight: 700;
    transition: background-color 250ms var(--timing-function);
    

    &:hover,
    &:focus {
        background-color:var(--primary-accent-color);
        color: var(--second-bgr-color);
        fill: var(--second-bgr-color);
    }
}

.header__menu--icon-close {
    width: 18px;
    height: 18px;
    margin-left: auto;
    margin-right: 20px;
    margin-top: 25px;
    cursor: pointer;
    fill: var(--second-bgr-color);
}

.header__burger-button{
    height: 12px;    
    padding: 0;  
    border: none;
    background-color: transparent;
    border-radius: 22px;  
    cursor: pointer;
    fill: var(--primary-accent-color);

        &--image{        
        width: 20px;
        height: 12px;

        &:hover,
        &:focus{
            background-color: var(--secondary-accent-color);
        }
    }
}

// hero
.girl__new__text,.trucks-text,.cafee-text{
    display: none;
}


.hero{
    display: flex;
    justify-content: space-between;
    position: relative;   
    background-color: var(--header-bgr-color);
    

    &--background{
        background-color: var(--header-bgr-color);
    }
    
    &__heading--second-color{
        color: var(--primary-accent-color);
    }

    &__button--dark{
        padding: 8px 13px;
        
        font-size: 8px;
        background-color: var(--primary-accent-color);
        color: var(--second-bgr-color);
        border-radius: 22px;
        transition: background-color 250ms var(--timing-function);

        &:hover,
        &:focus{
            background-color: var(--second-bgr-color);
            color: var(--primary-accent-color);
        }
    }

    &__button--light{
        padding: 8px 13px;
        font-size: 8px;
        background-color: var(--second-bgr-color);
        color: var(--primary-accent-color);
        border-radius: 22px;
        transition: background-color 250ms var(--timing-function);
       
        &:hover,
        &:focus{
            background-color: var(--primary-accent-color);
            color: var(--second-bgr-color);            
        }
    }

    &--milk-picture{
        display: flex;
    }

    &__paragraph{
        color: var(--second-bgr-color);
        font-size: 8px;
        line-height: 1.5;

        &-button{
            display: flex;
            margin-top: 20px;  
            align-items: center;
            justify-content: center;
            width: 26px;
            height: 26px;            
            border-radius: 50%;
            cursor: pointer;
            background-color: var(--second-bgr-color);
            fill: var(--primary-accent-color);
            border: none;
            transition: 250ms var(--timing-function);

            &:hover,
            &:focus{
            background-color:var(--primary-accent-color);
            fill: var(--second-bgr-color);
            }
        &--image{
            width: 11px;
            height: 10px;                       
        }
        }
    }

    &--right-section{
        display: flex;
        flex-direction: column;        
        align-items: flex-end;
    }

    &__circle-image{
        position: absolute;
        transition: transform 2500ms var(--timing-function);       
    }   
}

.unicorne-picture{
    display: none;
}

 .promocode {
     position: absolute;
     color: var(--primary-accent-color);
     text-align: center;
     font-family:'Titan one';
     letter-spacing: 0.04em;
     font-style: normal;
     font-size: 36px;
     font-weight: 400;
     top: 150px;
     right: 320px;
     text-transform: uppercase;
     border-radius: 25%;
     background-color: rgba(255, 165, 186, 0.2);
     transform: rotateZ(-90deg);
     z-index: 1;
     transition: transform 2500ms var(--timing-function);
    } 
    
    .ice-unicorn{
        font-size: 46px;
        color: snow;
    }

    .ice-unicorn2{
        color:cyan;
    }
    .unicorne1{
        color: yellow;
    }
    .unicorne2{
        color: indigo;
    }
    
    .unicorne3{
        color: blue;
    }

    .unicorne4{
        color: green;
    }

    .unicorne5{
        color: violet;
    }

    .unicorne6 {
    color: indigo;
    }

    .unicorne7 {
    color: red;
    }

    .unicorne8 {
    color: orangered;
    }   

    .hero__icecream-image {
     position: absolute;
     opacity: 1;
     transition: transform 2500ms var(--timing-function);     
 }

.input__hero{    
    position: absolute;
    width: 26px;
    height: 26px;
    left: 0;
    bottom: 0;
    opacity: 0;
    z-index: 6; 
    
}

.read-more{
    display: none;
    margin-bottom: 0;
    
}
.input__hero:checked~.read-more{
    display: block;
}

.input__hero:checked~.hero__paragraph-button{
    transform: rotateZ(-90deg);
}

.input__hero:hover~.hero__paragraph-button{
    background-color: var(--primary-accent-color);
    fill: var(--second-bgr-color);
}

// Modal

.backdrop-header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    z-index: 7;
    transition: transform 250ms var(--timing-function);

}

.is-hidden-header {
    opacity: 0;
    pointer-events: none;
    transform: scale(1.5);
    transition: transform 250ms var(--timing-function);
    
}

.modal-header {
    position: absolute;
    max-width: 528px;
    left: 50%;
    top: 50%;
    border-radius: 22px;
    transform: translate(-50%, -50%);
    background-color: var(--secondary-accent-color);
}

.modal-header__exit-button {
    display: flex;
    position: absolute;
    top: 8px;
    right: 8px;
    width: 30px;
    height: 30px;
    border: none;
    background-color: transparent;
    border-radius: 50%;
    align-items: center;
    fill: white;

    transition: fill 250ms var(--timing-function);

    &:hover {
        fill: var(--primary-accent-color);
    }

    &:active {
        background-color: pink;
    }
}

.modal-header__field {
    display: flex;
    padding: 40px;
    justify-content: center;
}

.modal-header__heading {
    display: flex;
    margin-bottom: 12px;
    font-family: 'Titan One';
    font-weight: 400;
    font-size: 30px;
    line-height: 23px;
    text-align: center;
    text-transform: uppercase;
    letter-spacing: 0.03em;
    color: var(--primary-accent-color);
}

.form__label-header {
    display: flex;
    right: auto;
    color: var(--primary-accent-color);
    font-size: 18px;
}

.form__field-header {
    position: relative;
}

.form__input-header {
    margin-top: 4px;
    margin-bottom: 10px;
    width: 100%;
    height: 40px;
    padding-left: 40px;
    border-width: 1px;
    outline: none;
    border-radius: 22px;
    border-color: white;

    &:focus-within {
        border-color: var(--primary-accent-color);
    }
}

.text-area-header {
    display: flex;
    left: auto;
    right: auto;
    margin-top: 4px;
    margin-bottom: 20px;
    width: 100%;
    height: 120px;
    padding: 12px 16px;
    border-width: 1px;
    border-radius: 22px;
    outline: none;
    resize: none;
    border-color: rgba(33, 33, 33, 0.2);
    font-size: 14px;

    &:focus-within {
        border-color: var(--primary-accent-color);
    }

    &::placeholder {
        color: rgba(212, 20, 67, 0.5);
        ;
    }
}

.modal__button-header {
    display: flex;
    margin-top: 30px;
    margin-left: auto;
    margin-right: auto;
    width: 200px;
    height: 50px;
    border: none;
    border-radius: 22px;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.88;
    letter-spacing: 0.06em;
    color: white;
    background-color: var(--primary-accent-color);
    transition: 250ms var(--timing-function);

    &:hover, 
    &:focus, 
    &:active {
        background-color:var(--second-bgr-color);
        color: var(--primary-accent-color);
    }
}


