h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

body.modal-open {
  overflow: hidden;
}

ul {
  margin: 0;
  padding-left: 0;
  list-style: none;
}

img {
  width: 100%;
  height: auto;
  display: block;
}

address {
  font-style: normal;
}

.section {
  padding-top: 60px;
  padding-bottom: 60px;
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (max-width: 767px) {
  .container {
    max-width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    min-width: 1200px;
    padding-left: 15px;
    padding-right: 16px;
  }
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.button-big {
  cursor: pointer;
  border: 0;
  border-radius: 22px;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
  display: inline-block;
}

.button-normal {
  cursor: pointer;
  border: 0;
  border-radius: 20px;
  font-family: DM Sans;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
  display: inline-block;
}

.button-micro {
  cursor: pointer;
  border: 0;
  border-radius: 20px;
  font-family: DM Sans;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
  display: inline-block;
}

:root {
  --first-bgr-color: #fff5f6;
  --second-bgr-color: #fff;
  --header-bgr-color: #ffb8ca;
  --primary-accent-color: #d41443;
  --title-color: #d41443;
  --secondary-accent-color: #ffa5ba;
  --main-text-color: #fff;
  --secondary-text-color: #000;
  --third-text-color: #907e82;
  --bgr-color-ice-cream: #ffb8ca;
  --bgr-color-ice-coffee: #f0d1a5;
  --bgr-color-milkshakes: #c2e297;
  --first-btn-color: #d41443;
  --second-btn-color: #fff;
  --hover-btn: #ecebeb;
  --hover-contacts: #d41443;
  --hover-icon-social: #d41443;
  --timing-function: cubic-bezier(.4, 0, .2, 1);
}

a {
  color: var(--second-bgr-color);
  text-decoration: none;
}

h1 {
  color: var(--second-bgr-color);
  letter-spacing: .04em;
  text-transform: uppercase;
  font-family: Titan One;
  font-style: normal;
  font-weight: 400;
}

.header {
  align-items: center;
  display: flex;
}

.header__link {
  transition: color 250ms var(--timing-function);
}

.header--background {
  background-color: var(--header-bgr-color);
}

.header__menu {
  width: 254px;
  visibility: hidden;
  opacity: 0;
  height: 100%;
  z-index: 5;
  background-color: #e17992;
  flex-direction: column-reverse;
  justify-content: flex-end;
  display: flex;
  position: fixed;
  top: 0;
  left: 40%;
  right: 0;
  transform: translateX(100vw);
}

.header__menu--icon-close {
  transition: 250ms var(--timing-function);
}

.header__menu--icon-close:hover {
  box-shadow: 5px 5px 5px var(--primary-accent-color);
}

.header__menu.is-open {
  visibility: visible;
  opacity: 1;
  transition: transform 400ms var(--timing-function);
  transform: translateX(0);
}

.nav {
  display: flex;
}

.nav__links {
  list-style: none;
}

.nav__links--desktop {
  display: none;
}

.nav__button {
  height: 44px;
  width: 160px;
  background-color: var(--second-bgr-color);
  color: var(--primary-accent-color);
  cursor: pointer;
  fill: var(--primary-accent-color);
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  display: flex;
}

.nav__button:hover, .nav__button:focus {
  background-color: var(--primary-accent-color);
  color: var(--second-bgr-color);
  fill: var(--second-bgr-color);
}

.nav__button-arrow {
  width: 9px;
  height: 11px;
  margin-left: 12px;
}

.header__button {
  height: 44px;
  width: 160px;
  background-color: var(--second-bgr-color);
  color: var(--primary-accent-color);
  fill: var(--primary-accent-color);
  cursor: pointer;
  transition: background-color 250ms var(--timing-function);
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  display: flex;
}

.header__button:hover, .header__button:focus {
  background-color: var(--primary-accent-color);
  color: var(--second-bgr-color);
  fill: var(--second-bgr-color);
}

.header__menu--icon-close {
  width: 18px;
  height: 18px;
  cursor: pointer;
  fill: var(--second-bgr-color);
  margin-top: 25px;
  margin-left: auto;
  margin-right: 20px;
}

.header__burger-button {
  height: 12px;
  cursor: pointer;
  fill: var(--primary-accent-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 22px;
  padding: 0;
}

.header__burger-button--image {
  width: 20px;
  height: 12px;
}

.header__burger-button--image:hover, .header__burger-button--image:focus {
  background-color: var(--secondary-accent-color);
}

.girl__new__text, .trucks-text, .cafee-text {
  display: none;
}

.hero {
  background-color: var(--header-bgr-color);
  justify-content: space-between;
  display: flex;
  position: relative;
}

.hero--background {
  background-color: var(--header-bgr-color);
}

.hero__heading--second-color {
  color: var(--primary-accent-color);
}

.hero__button--dark {
  background-color: var(--primary-accent-color);
  color: var(--second-bgr-color);
  transition: background-color 250ms var(--timing-function);
  border-radius: 22px;
  padding: 8px 13px;
  font-size: 8px;
}

.hero__button--dark:hover, .hero__button--dark:focus {
  background-color: var(--second-bgr-color);
  color: var(--primary-accent-color);
}

.hero__button--light {
  background-color: var(--second-bgr-color);
  color: var(--primary-accent-color);
  transition: background-color 250ms var(--timing-function);
  border-radius: 22px;
  padding: 8px 13px;
  font-size: 8px;
}

.hero__button--light:hover, .hero__button--light:focus {
  background-color: var(--primary-accent-color);
  color: var(--second-bgr-color);
}

.hero--milk-picture {
  display: flex;
}

.hero__paragraph {
  color: var(--second-bgr-color);
  font-size: 8px;
  line-height: 1.5;
}

.hero__paragraph-button {
  width: 26px;
  height: 26px;
  cursor: pointer;
  background-color: var(--second-bgr-color);
  fill: var(--primary-accent-color);
  transition: 250ms var(--timing-function);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  display: flex;
}

.hero__paragraph-button:hover, .hero__paragraph-button:focus {
  background-color: var(--primary-accent-color);
  fill: var(--second-bgr-color);
}

.hero__paragraph-button--image {
  width: 11px;
  height: 10px;
}

.hero--right-section {
  flex-direction: column;
  align-items: flex-end;
  display: flex;
}

.hero__circle-image {
  transition: transform 2500ms var(--timing-function);
  position: absolute;
}

.unicorne-picture {
  display: none;
}

.promocode {
  color: var(--primary-accent-color);
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  z-index: 1;
  transition: transform 2500ms var(--timing-function);
  background-color: rgba(255, 165, 186, .2);
  border-radius: 25%;
  font-family: Titan one;
  font-size: 36px;
  font-style: normal;
  font-weight: 400;
  position: absolute;
  top: 150px;
  right: 320px;
  transform: rotateZ(-90deg);
}

.ice-unicorn {
  color: snow;
  font-size: 46px;
}

.ice-unicorn2 {
  color: #0ff;
}

.unicorne1 {
  color: #ff0;
}

.unicorne2 {
  color: indigo;
}

.unicorne3 {
  color: #00f;
}

.unicorne4 {
  color: green;
}

.unicorne5 {
  color: violet;
}

.unicorne6 {
  color: indigo;
}

.unicorne7 {
  color: red;
}

.unicorne8 {
  color: #ff4500;
}

.hero__icecream-image {
  opacity: 1;
  transition: transform 2500ms var(--timing-function);
  position: absolute;
}

.input__hero {
  width: 26px;
  height: 26px;
  opacity: 0;
  z-index: 6;
  position: absolute;
  bottom: 0;
  left: 0;
}

.read-more {
  margin-bottom: 0;
  display: none;
}

.input__hero:checked ~ .read-more {
  display: block;
}

.input__hero:checked ~ .hero__paragraph-button {
  transform: rotateZ(-90deg);
}

.input__hero:hover ~ .hero__paragraph-button {
  background-color: var(--primary-accent-color);
  fill: var(--second-bgr-color);
}

.backdrop-header {
  width: 100%;
  height: 100%;
  z-index: 7;
  transition: transform 250ms var(--timing-function);
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.is-hidden-header {
  opacity: 0;
  pointer-events: none;
  transition: transform 250ms var(--timing-function);
  transform: scale(1.5);
}

.modal-header {
  max-width: 528px;
  background-color: var(--secondary-accent-color);
  border-radius: 22px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal-header__exit-button {
  width: 30px;
  height: 30px;
  fill: #fff;
  transition: fill 250ms var(--timing-function);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  align-items: center;
  display: flex;
  position: absolute;
  top: 8px;
  right: 8px;
}

.modal-header__exit-button:hover {
  fill: var(--primary-accent-color);
}

.modal-header__exit-button:active {
  background-color: pink;
}

.modal-header__field {
  justify-content: center;
  padding: 40px;
  display: flex;
}

.modal-header__heading {
  text-align: center;
  text-transform: uppercase;
  letter-spacing: .03em;
  color: var(--primary-accent-color);
  margin-bottom: 12px;
  font-family: Titan One;
  font-size: 30px;
  font-weight: 400;
  line-height: 23px;
  display: flex;
}

.form__label-header {
  color: var(--primary-accent-color);
  font-size: 18px;
  display: flex;
  right: auto;
}

.form__field-header {
  position: relative;
}

.form__input-header {
  width: 100%;
  height: 40px;
  border-width: 1px;
  border-color: #fff;
  border-radius: 22px;
  outline: none;
  margin-top: 4px;
  margin-bottom: 10px;
  padding-left: 40px;
}

.form__input-header:focus-within {
  border-color: var(--primary-accent-color);
}

.text-area-header {
  width: 100%;
  height: 120px;
  resize: none;
  border-width: 1px;
  border-color: rgba(33, 33, 33, .2);
  border-radius: 22px;
  outline: none;
  margin-top: 4px;
  margin-bottom: 20px;
  padding: 12px 16px;
  font-size: 14px;
  display: flex;
  left: auto;
  right: auto;
}

.text-area-header:focus-within {
  border-color: var(--primary-accent-color);
}

.text-area-header::-ms-input-placeholder {
  color: rgba(212, 20, 67, .5);
}

.text-area-header::placeholder {
  color: rgba(212, 20, 67, .5);
}

.modal__button-header {
  width: 200px;
  height: 50px;
  letter-spacing: .06em;
  color: #fff;
  background-color: var(--primary-accent-color);
  transition: 250ms var(--timing-function);
  border: none;
  border-radius: 22px;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.88;
  display: flex;
}

.modal__button-header:hover, .modal__button-header:focus, .modal__button-header:active {
  background-color: var(--second-bgr-color);
  color: var(--primary-accent-color);
}

@media screen and (max-width: 767px) {
  .header {
    justify-content: space-between;
    padding-top: 8px;
    overflow: hidden;
  }

  .header__button {
    display: none;
  }

  .header__menu {
    width: 250px;
    left: auto;
    right: 0;
  }

  .header__input {
    width: 50px;
    height: 20px;
    cursor: pointer;
    opacity: 0;
    z-index: 6;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    position: fixed;
    top: 23px;
    left: 0;
    right: 0;
    transform: translateX(230%);
  }

  .nav__button {
    width: calc(100% - 40px);
    margin-top: 26px;
    margin-left: 20px;
    font-size: 16px;
  }

  .nav__links {
    width: 100%;
    margin-top: 19px;
    font-size: 14px;
    font-weight: 700;
  }

  .nav__links > li {
    border-top: 1px solid #e18298;
    border-bottom: 1px solid #e18298;
    padding-top: 11px;
    padding-bottom: 13px;
    padding-left: 20px;
  }

  .container {
    position: static;
  }

  .hero {
    height: 446px;
  }

  .hero--milk-picture, .hero--right-section, .promocode {
    display: none;
  }

  .hero__circle-image {
    z-index: 1;
    top: 83px;
    right: 0;
  }

  .hero__icecream-image {
    z-index: 2;
    top: 94px;
    right: 30px;
  }

  .hero--left-section {
    z-index: 3;
    position: absolute;
    top: 136px;
    left: 20px;
  }

  .hero__heading {
    margin-bottom: 20px;
    font-size: 26px;
  }

  .hero__heading--second-color {
    font-size: 48px;
  }

  .modal-header {
    width: calc(100% - 30px);
  }
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .header {
    justify-content: space-between;
    padding-top: 32px;
  }

  .header__burger-button {
    z-index: 4;
    margin-left: 492px;
    position: absolute;
    transform: translateX(13%);
  }

  .header__menu {
    width: 254px;
  }

  .nav__button {
    width: 186px;
    margin-top: 26px;
    margin-left: auto;
    margin-right: auto;
    font-size: 16px;
  }

  .nav__links {
    width: 100%;
    margin-top: 19px;
    font-size: 14px;
    font-weight: 700;
  }

  .nav__links > li {
    border-top: 1px solid #e18298;
    border-bottom: 1px solid #e18298;
    padding-top: 11px;
    padding-bottom: 13px;
    padding-left: 20px;
  }

  .header__menu--icon-close {
    width: 18px;
    height: 18px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: 20px;
  }

  .promocode {
    display: none;
  }

  .hero__heading {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 22px;
  }

  .hero__heading--second-color {
    font-size: 34px;
  }

  .hero--milk-picture {
    margin-top: 20px;
    margin-bottom: 16px;
  }

  .hero__description {
    z-index: 3;
    position: absolute;
    bottom: 56px;
    left: 184px;
  }

  .hero__paragraph {
    width: 114px;
  }

  .hero--right-section {
    margin-top: 16px;
  }

  .hero__number-16 {
    margin-bottom: 16px;
  }

  .hero__circle-image {
    z-index: 1;
    bottom: 40px;
    left: 248px;
  }

  .hero__icecream-image {
    z-index: 2;
    bottom: 0;
    left: 322px;
  }

  .hero__girl-image {
    z-index: 3;
    position: absolute;
    bottom: 0;
    right: 32px;
  }

  .modal-header {
    width: 450px;
    margin-top: 20px;
  }
}

@media screen and (min-width: 1200px) {
  .header__burger-button, .header__menu--icon-close, .nav__button, .header__input {
    display: none;
  }

  .header {
    justify-content: space-between;
    padding-top: 37px;
    position: relative;
  }

  .header__link:hover, .header__link:focus {
    color: var(--primary-accent-color);
  }

  .nav__links--desktop {
    width: max-content;
    z-index: 3;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
    font-size: 16px;
    font-weight: 700;
    display: flex;
    position: absolute;
    top: 52px;
    left: 0;
    right: 0;
  }

  .nav__links--desktop > li:not(:last-of-type) {
    margin-right: 35px;
  }

  .hero__heading {
    margin-top: 60px;
    margin-bottom: 24px;
    font-size: 38px;
  }

  .hero__heading--second-color {
    font-size: 58px;
  }

  .hero__button--dark {
    background-color: var(--primary-accent-color);
    color: var(--second-bgr-color);
    border-radius: 22px;
    margin-right: 10px;
    padding: 11px 20px;
    font-size: 14px;
  }

  .hero__button--light {
    background-color: var(--second-bgr-color);
    color: var(--primary-accent-color);
    border-radius: 22px;
    padding: 11px 20px;
    font-size: 14px;
  }

  .hero--milk-picture {
    margin-top: 40px;
    margin-bottom: 49px;
  }

  .hero__description {
    margin-left: 229px;
    padding-top: 69px;
    position: absolute;
  }

  .hero__paragraph {
    width: 200px;
    font-size: 14px;
    line-height: 1.4;
  }

  .hero--right-section {
    margin-top: 68px;
  }

  .cafee-text {
    z-index: 4;
    color: rgba(0, 0, 0, 0);
    text-transform: uppercase;
    transition: all 500ms var(--timing-function);
    background-color: rgba(0, 0, 0, 0);
    font-family: Titan one;
    font-size: 24px;
    display: block;
    position: absolute;
    bottom: 150px;
    right: 173px;
  }

  .hero__number-16 {
    transition: transform 1500ms var(--timing-function);
    margin-bottom: 15px;
  }

  .hero__number-16:hover {
    transform: scale(1.4);
  }

  .hero__number-16:hover ~ .hero__number-23 {
    transform: translate(0, 20%);
  }

  .hero__number-16:hover ~ .cafee-text {
    color: var(--primary-accent-color);
    background-color: #fff;
  }

  .trucks-text {
    z-index: 4;
    color: rgba(0, 0, 0, 0);
    text-transform: uppercase;
    transition: all 500ms var(--timing-function);
    background-color: rgba(0, 0, 0, 0);
    font-family: Titan one;
    font-size: 16px;
    display: block;
    position: absolute;
    bottom: 150px;
    right: 173px;
  }

  .hero__number-23 {
    transition: transform 2500ms var(--timing-function);
    margin-bottom: 91px;
  }

  .hero__number-23:hover {
    transform: rotateZ(-360deg);
  }

  .hero__number-23:hover ~ .trucks-text {
    color: var(--primary-accent-color);
    background-color: #fff;
  }

  .hero__circle-image {
    z-index: 1;
    bottom: 65px;
    right: 149px;
  }

  .hero__circle-image:hover {
    transform: scale(1.1);
  }

  .unicorne-picture {
    width: 300px;
    z-index: 1;
    transition: transform 2500ms var(--timing-function);
    display: block;
    position: absolute;
    bottom: 180px;
    right: 300px;
  }

  .hero__icecream-image {
    z-index: 2;
    bottom: 0;
    right: 331px;
  }

  .hero__icecream-image:hover {
    transform: translateX(-100%);
  }

  .hero__icecream-image:hover ~ .promocode {
    transform: rotate(0);
  }

  .hero__icecream-image:hover ~ .unicorne-picture {
    transform: scale(2);
  }

  .girl__new__text {
    z-index: 3;
    color: rgba(0, 0, 0, 0);
    text-transform: uppercase;
    transition: all 500ms var(--timing-function);
    background-color: rgba(0, 0, 0, 0);
    font-family: Titan one;
    font-size: 24px;
    display: block;
    position: absolute;
    bottom: 150px;
    right: 173px;
  }

  .hero__girl-image {
    z-index: 3;
    transition: all 2500ms var(--timing-function);
    position: absolute;
    bottom: 0;
    right: 20px;
  }

  .hero__girl-image:hover ~ .girl__new__text {
    color: var(--primary-accent-color);
    background-color: #fff;
  }
}

.product-box {
  text-align: center;
}

.product-box__label {
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--secondary-accent-color);
  font-family: Titan One;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.16;
}

